import {DocumentTextIcon} from '@sanity/icons'
import { defineField, defineType} from 'sanity'

export const postType = defineType({
  name: 'post',
  title: 'Post',
  type: 'document',
  icon: DocumentTextIcon,
  fields: [
    defineField({
      name: 'title',
      type: 'string',
      validation: rule => rule.required().min(10).max(80)
    }),
    defineField({
      name: 'slug',
      type: 'slug',
      options: {
        source: 'title',
        maxLength: 96,
      },
    }),
    defineField({
      name: 'author',
      type: 'reference',
      to: {type: 'author'},
    }),
    defineField({
      name: 'mainImage',
      type: 'image',
      options: {
        hotspot: true,
      },
      fields: [
        defineField({
          name: 'alt',
          type: 'string',
          title: 'Alternative text',
        })
      ]
    }),
    // for category
    defineField({
      name: 'category',
      type: 'reference',
      to: {type: 'category'},
    }),
    
    defineField({
      name: 'publishedAt',
      type: 'datetime',
    }),
    defineField({
      name: 'body',
      type: 'blockContent',
    }),
    // for  kewords
    defineField({
      name: 'keywords',
      title: 'Keywords',
      type: 'string',
      initialValue : 'pingprospect, lead generation, facebook lead gen, facebook DM, facebook DM bot, Facebook prospecting, Facebook lead generation,',
    }),
    // for meta description
    defineField({
      name: 'meta_description',
      title: 'Meta Description',
      type: 'string',
      validation: rule => rule.required().min(10).max(100)
    }),
    
    // for post status 
    defineField({
      name: 'status',
      title: 'status',
      type: 'string',
      initialValue : 'Draft',
      validation: rule => rule.required().max(5).warning("Values can be 'Live' or 'Draft' ").error("Values can be 'Live' or 'Draft' ")
    }),
  ],
  preview: {
    select: {
      title: 'title',
      author: 'author.name',
      media: 'mainImage',
    },
    prepare(selection) {
      const {author} = selection
      return {...selection, subtitle: author && `by ${author}`}
    },
  },
})
